*{
  box-sizing: border-box;

} 
body {
  margin: 0;
  padding: 0;
}



.wrapper {

    padding-top: 50px;
    padding-bottom: 50px;
    /* padding-inline-start: 200px; */
    /* padding-inline-end: 200px; */
    background: #ECEFF1;
    min-height: 80%;
    width: 90%;
    width: 700px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    
    -webkit-justify-content: center;
    
            justify-content: center; 
    /* align-items: center; 
    overflow: auto; */
    margin: 0 auto;
  }
  
.card {
    background: #fff;
    border-radius: 5px;
    height: 150px;
    margin-top: 10px;
    margin-right: 10px;
    position: relative;
    width: 150px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    background: yellow;
  }
  .card > .img-container > img {
    width: 90%;
    height: 90%;
    margin: 7px;
  }
h5,
h6 {
  background-color: yellowgreen;
  display: inline;
  cursor: default;
  
  
}

h5 {
  font-size: 3.0em;
  padding: 10px;

  margin-bottom: 25px;
}
h6 {
    font-size: 1.1em;
    padding: 5px;
    margin-right: 100px;
    margin-top: 30px;

  }

h5:hover {
  color: #fff;
  background-color: #000;
  display: inline;
  
}

h6:hover {
  color: #fff;
  background-color: red;
  display: inline;
}

.scoreB {
  margin-bottom: 25px;
}

.col-left {
  font-family: "Major Mono Display", monospace;
  text-align: center;
  

}

.col-right {
  font-family: "Noto Serif SC", serif;
  text-align: right;
  padding-top: 20px;
  padding-right: 30px;
}
#note {
    font-size: 25px;
    text-align: center;
}
.alert-danger {
  font-family: "Noto Serif SC", serif;
  text-transform: uppercase;
  color: #fff;
  /* display: inline; */
  background-color: red !important;
  border-radius: 0 !important;
  border-color: none !important;
  border: 0 !important;
  padding: 0 !important;
  margin: 0 !important;
  cursor: default;
  /* margin: auto; */
}

.alert-success:hover {
  color: #fff;
  background-color: #000 !important;
}

#root {
  background-color: #dddddd;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  /* margin-bottom: -100px; */
}

